import axios from 'axios'

export default {
    state: {
        points: [],
        labels: [],
        values: [],
        queries: []
    },

    mutations: {
        getPoints(state, data) {
            state.points = data
            state.points.splice(0, state.points.length - 7)
            state.labels = []
            state.values = []
            state.queries = []
        }
    },

    actions: {
        async getPoints({ commit }) {
            const points = await axios.get('/api/graph')
            commit('getPoints', points.data)
        }
    },

    getters: {
        pointsGetter: state => state.points,

        labelsGetter: state => {
            state.points.forEach(point => {
                state.labels.push(`${point.value} (${new Date(point.dateTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', hour12: false })})`)
            })
            return state.labels
        },

        valuesGetter: state => {
            state.points.forEach(point => {
                state.values.push(point.value)
            })
            return state.values
        },

        queriesGetter: state => {
            state.points.forEach(point => {
                state.queries.push(point.query)
            })
            return state.queries
        }

    }
}