<template>
  <v-app id="inspire">
    <v-app-bar app dark>
      <v-icon style="margin-right: 10px;">fa-robot</v-icon>
      <v-toolbar-title
        v-if="$route.path !== '/'"
        style="cursor: pointer;"
        @click="$router.push('/')"
        >Newser</v-toolbar-title
      >
      <v-toolbar-title v-else style="cursor: pointer;">Newser</v-toolbar-title>
      <v-icon v-if="started && onLine" style="margin-left: 10px; color:green"
        >fa-circle</v-icon
      >
      <v-icon v-if="stopped && onLine" style="margin-left: 10px; color:yellow"
        >fa-circle</v-icon
      >
      <v-icon v-if="!onLine" style="margin-left: 10px; color:red"
        >fa-circle</v-icon
      >
      <v-spacer></v-spacer>
      <p
        v-if="$route.path !== '/stat'"
        style="font-size: 20px; margin-top: 15px; margin-right: 20px; cursor: pointer"
        @click="$router.push('/stat')"
      >
        Статистика
      </p>
      <p
        v-else
        style="font-size: 20px; margin-top: 15px; margin-right: 20px; cursor: pointer"
      >
        Статистика
      </p>
      <p style="font-size: 20px; margin-top: 15px;">{{ getUser }}</p>
      <v-btn
        style="margin-left:10px; width: 20px; background-color:white; color: black"
        @click="$store.dispatch('logout')"
        ><v-icon>fa-sign-out-alt</v-icon></v-btn
      >
    </v-app-bar>
    <v-overlay :value="starting" opacity="0.95"
      ><p style="font-size: 25px;">Подготовка к запуску!</p>
      <div align="center">
        <v-progress-circular
          width="7"
          size="80"
          color="white"
          indeterminate
        ></v-progress-circular>
      </div>
    </v-overlay>
    <main role="main" class="container">
      <v-overlay :value="!isLoggedIn" opacity="0.95">
        <p
          style="
        width: 250px;
        font-size: 20px;
        text-align: center;
        "
        >
          Чтобы управлять поисковым ботом необходимо авторизоваться в системе!
        </p>
        <v-text-field placeholder="Логин" v-model="login"></v-text-field>
        <v-text-field
          type="password"
          placeholder="Пароль"
          v-model="password"
        ></v-text-field>
        <v-btn style="width: 100%" color="orange lighten-2" @click="signIn">
          Войти
        </v-btn>
      </v-overlay>
      <router-view :messages="messages" :isOnline="onLine" />
      <v-footer style="margin-top: 50px;" fixed class="font-weight-medium">
        <v-col class="text-center" cols="12">
          <v-btn
            :disabled="started || !onLine"
            @click="startDialog = true"
            style="margin-left:10px; background-color:green; color:white"
            ><v-icon>fa-play</v-icon></v-btn
          >
          <v-btn
            :disabled="stopped || !onLine"
            @click="stopBot"
            style="margin-left:10px; background-color:red; color:white"
            ><v-icon>fa-stop</v-icon></v-btn
          >
          <v-btn
            :disabled="messages.length == 0"
            @click="messages = []"
            style="margin-left:10px; background-color:blue; color:white"
            ><v-icon>fa-broom</v-icon></v-btn
          >
          <v-btn
            style="margin-left:10px; background-color:grey; color:white"
            @click="$router.push('/config')"
            ><v-icon>fa-cogs</v-icon></v-btn
          >
          <v-btn
            style="margin-left:10px; background-color:grey; color:white"
            color="red lighten-2"
            dark
            @click="dialog = !dialog"
          >
            Ввести запрос
          </v-btn>
        </v-col>

        <div class="text-center">
          <v-dialog v-model="dialog" width="500">
            <v-card>
              <v-card-title class="headline grey lighten-2">
                Ввести нестандартный запрос
              </v-card-title>

              <v-card-text style="padding:10px;">
                Это действие откроет параллельный поток работы бота и обработает
                введенный ниже запрос
              </v-card-text>

              <v-divider></v-divider>

              <div style="padding:10px;">
                <v-text-field label="Запрос" v-model="query"></v-text-field>
              </div>

              <div style="padding:10px;">
                <v-select
                  :items="items"
                  v-model="engine"
                  label="Поисковая система"
                  outlined
                ></v-select>
              </div>

              <div style="padding:10px;">
                <v-text-field label="ID запроса" type="number" v-model="id_request"></v-text-field>
              </div>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="green"
                  text
                  @click="
                    startBot('custom');
                    dialog = false;
                  "
                >
                  Запустить
                </v-btn>
                <v-btn color="red" text @click="dialog = false">
                  Закрыть
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>

        <v-row justify="center">
          <v-dialog
            v-model="startDialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
          >
            <v-card>
              <v-toolbar dark>
                <v-btn icon dark @click="startDialog = false">
                  <v-icon>fa-times</v-icon>
                </v-btn>
                <v-toolbar-title>Настройки запуска</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                  <v-btn
                    dark
                    text
                    @click="
                      startDialog = false;
                      startBot('manual');
                    "
                  >
                    Запустить
                  </v-btn>
                </v-toolbar-items>
              </v-toolbar>
              <v-list three-line subheader>
                <v-subheader>Параметры бота</v-subheader>

                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title
                      >Глубина поиска (в страницах)</v-list-item-title
                    >
                    <v-text-field
                      v-model="pages"
                      required
                      type="number"
                    ></v-text-field>
                  </v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title>Источник запросов</v-list-item-title>
                    <div style="padding:10px;">
                      <v-select
                        v-model="source"
                        :items="sources"
                        :value="sources[0]"
                        label="Поисковая система"
                        outlined
                      ></v-select>
                    </div>
                  </v-list-item-content>
                </v-list-item>
                <div v-if="source === 'Удаленный сервер'" style="padding:10px;">
                  <v-text-field
                    required
                    placeholder="URl адрес сервера"
                    v-model="serverUrl"
                  ></v-text-field>
                </div>
              </v-list>
              <v-divider></v-divider>
              <v-list two-line subheader>
                <v-subheader>Выбор поисковых систем</v-subheader>
                <v-list-item v-for="(place, index) in newsPlaces" :key="index">
                  <v-list-item-action>
                    <v-checkbox v-model="engines[index]"></v-checkbox>
                  </v-list-item-action>
                  <v-list-item-content>
                    <v-list-item-title>{{ place }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-card>
          </v-dialog>
        </v-row>
      </v-footer>
    </main>
  </v-app>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  created() {
    this.source = this.sources[0];
  },

  computed: {
    ...mapGetters(["getUser"]),

    isLoggedIn() {
      for (let key in this.getUser) {
        return true;
      }
      return false;
    },
  },

  data: () => ({
    pages: 1,
    newsPlaces: ["Yandex.News", "Google.News", "Rambler.News"],

    messages: [],

    stopped: false,
    started: false,
    onLine: false,

    stopping: false,
    starting: false,

    login: "",
    password: "",
    dialog: false,
    startDialog: false,

    query: "",
    id_request: 0,
    items: ["Yandex.News", "Rambler.News", "Google.News"],
    engine: "",
    engines: [false, false, false],
    sources: ["Удаленный сервер", "Локальный файл"],
    source: "",
    serverUrl: ""
  }),

  sockets: {
    disconnect: function() {
      this.onLine = false;
    },

    captcha: function() {
      this.$socket.emit("captcha");
    },

    connect: function() {
      this.onLine = true;
      this.stopped = true;
      this.onLine = true;
      this.started = false;
      this.stopped = false;
    },

    message: function(message) {
      if (message.code === 0) {
        this.stopped = true;
        this.started = false;
      } else {
        this.stopped = false;
        this.started = true;
      }

      if (message.message === "Бот остановлен!") {
        this.stopping = false;
      }

      if (message.code == 1) {
        this.starting = false;
      }

      this.messages.unshift(message);
    },
  },

  methods: {
    startBot(type) {
      this.messages.unshift({
        message: "Запустить бота",
        sender: "Оператор",
        subtitle: "Команда",
      });

      if (type === 'custom') {
        if (this.engine === "Yandex.News") this.engines[0] = true
        if (this.engine === "Google.News") this.engines[1] = true
        if (this.engine === "Rambler.News") this.engines[2] = true
        this.source = 'Веб-приложение'
        this.pages = 1
      }

      this.$socket.emit("startBot", {
        source: this.source,
        query: this.query || '',
        id_request: this.id_request,
        pages: this.pages,
        url: this.serverUrl || '',
        engines: {
          4: this.engines[0],
          3: this.engines[1],
          7: this.engines[2],
        },
      })

      this.engine = ''
      this.query = ''
      this.engines = [false, false, false]

      this.starting = true;
    },

    stopBot() {
      this.messages.unshift({
        message: "Остановить бота",
        sender: "Оператор",
        subtitle: "Команда",
      });

      this.$socket.emit("stopBot");
      this.stopping = true;
    },

    signIn() {
      const user = {
        login: this.login,
        password: this.password,
      };

      this.$store.dispatch("login", user);
    },
  }
};
</script>
