import Vue from "vue";
import VueRouter from "vue-router";
import Main from '../pages/Main'
import Config from '../pages/Config'
import Stat from './../pages/Stat'

Vue.use(VueRouter);

const routes = [{
        path: '/',
        name: 'Main',
        component: Main
    },
    {
        path: '/config',
        name: 'Config',
        component: Config
    },
    {
        path: '/stat',
        name: 'Stat',
        component: Stat
    }
];

const router = new VueRouter({
    routes
});

export default router;