<template>
  <v-container style="margin-top: 50px;">
    <v-card
    class="mt-4 mx-auto"
  >
    <v-sheet
      class="v-sheet--offset mx-auto"
      color="cyan"
      elevation="12"
      max-width="calc(100% - 32px)"
    >
      <v-sparkline
        :labels="labelsGetter"
        :value="valuesGetter"
        color="white"
        line-width="2"
        padding="25"
      ></v-sparkline>
    </v-sheet>

    <v-card-text class="pt-0">
      <div class="title font-weight-light mb-2">
        Количество полученных новостей
      </div>
      <v-divider class="my-2"></v-divider>
      <v-icon
        class="mr-2"
        small
      >
        fa-clock
      </v-icon>
      <span class="caption grey--text font-weight-light">Последнее обновление {{ labelsGetter[labelsGetter.length - 1] }}</span><br /><br/>
      <p class="caption grey--text font-weight-light" v-for="(point, index) in pointsGetter" :key="index">{{ point.query }} - {{ new Date(point.dateTime).toLocaleString() }} - {{ point.value }} новостей</p>
    </v-card-text>
  </v-card>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {

    data() {
        return {
            interval: ''
        }
    },

  async mounted() {
    await this.getData()
  },

  methods: {
    ...mapActions(["getPoints"]),

    async getData() {
        this.getPoints()
        this.interval = setInterval(() => {
            this.getPoints()
        }, 5000);
    }
  },

  computed: {
    ...mapGetters(["pointsGetter", "labelsGetter", "valuesGetter", "queriesGetter"]),
  },
};
</script>

<style scoped>
  .v-sheet--offset {
    top: -24px;
    position: relative;
  }
</style>