import axios from 'axios'
export default {
	state: {
		config: []
	},
	mutations: {
		getConfig(state, data) {
			state.config = data
		},
		updateConfig(state, data) {
			state.config = data
		}
	},
	actions: {
		async getConfig({commit}) {
			await axios.get('http://localhost:5000/api/config')
			.then(res => {
				console.log(res.data.config)
				commit('getConfig', res.data.config)
			})
		},

		async updateConfig({commit}, newConfig) {
			commit('updateConfig', newConfig)
			await axios.post('http://localhost:5000/api/updateConfig', {
				config: newConfig
			})
			.then(res => {
				console.log(res.data)
			})
			.catch(err => {
				console.log(`Перезапускается сервер ${err}`)
			})
		}
	},
	getters: {
		config: state => JSON.stringify(state.config)
	}
}