<template>
	<v-container style="margin-top: 50px;">
		<v-card class="mx-auto" flat>
			<v-card-title style="text-align: center">Текущая конфигурация</v-card-title>
			<p style="color:red">{{ errorMessage }}</p>
			<p style="color:green">{{ successMessage }}</p>

			<vue-json-editor 
				v-model="config" 
				:show-btns="true"
				:expandedOnStart="false" 
				@json-save="onJsonChange"
				style="margin-bottom:60px;"
				>
			</vue-json-editor>
		</v-card>

		<v-overlay
        	:value="overlay"
        	opacity="0.95"
      	>
        	<p style="
        		width: 250px;
        		font-size: 20px;
        		text-align: center;
        	">
        		Производится перезапуск системы с новыми параметрами...
        	</p>
        	<div align="center">
        		<v-progress-circular
			        width="7"
			        size="80"
			        color="white"
			        indeterminate
		      	></v-progress-circular>
		  	</div>
      </v-overlay>

	</v-container>
</template>

<script>
import vueJsonEditor from 'vue-json-editor'
export default {
	components: {
		vueJsonEditor
	},
	data() {
		return {
			newConfig: localStorage.getItem('config'),
			overlay: false,
			errorMessage: null,
			successMessage: null
		}
	},
	async created() {
		await this.$store.dispatch('getConfig')
	},
	computed: {
		config: {
			get() {
				localStorage.setItem('config', JSON.stringify(this.$store.getters.config))
				return JSON.parse(this.$store.getters.config)
			},
			set(value) {
				this.newConfig = value
			}
		}
	},
	methods: {
		async updateConfig() {
			this.overlay = true
			try {
				console.log(JSON.parse(this.newConfig))
				this.errorMessage = ''
				await this.$store.dispatch('updateConfig', this.newConfig)
			} catch (err) {
				this.overlay = false
				console.log(`Ошибка JSON ${err}`)
				this.errorMessage = `Проверьте правильность заполнения JSON файла!`
			}
		},
		async onJsonChange (value) {
			this.overlay = true
	        try {
	        	console.log(value)
				this.errorMessage = ''
				await this.$store.dispatch('updateConfig', value)
			} catch (err) {
				this.overlay = false
				console.log(`Ошибка JSON ${err}`)
				this.errorMessage = `Проверьте правильность заполнения JSON файла!`
			}
		}
	},
	sockets: {
	    connect: function () {
	        this.overlay = false
	        // this.$router.push('/')
	        this.successMessage = `Конфигурация успешно обновлена ${new Date(Date.now())}`
	    },
	    message: function(message) {
		    if (message.message.indexOf('Начинаю') !== -1) {
	          this.starting = false
	        }
	    }
	}
}
</script>