<template>
	<div
        class="mx-auto"
        style="
        margin: 0 auto; margin-top:105px; margin-bottom: 10px"
    >

    <div align="center" style="margin-top: 30vh" v-if="messages.length === 0 && isOnline">
      <v-icon @click="startBot" size="200">fa-play-circle</v-icon>
    </div>
    
    <v-overlay
      :value="starting"
      opacity="0.95"
    ><p style="font-size: 25px;">Подготовка к запуску!</p>
      <div align="center"><v-progress-circular
        width="7"
        size="80"
        color="white"
        indeterminate
      ></v-progress-circular></div>
    </v-overlay>

  	<v-card v-for="(message, index) in messages" :key="index" style="margin-bottom: 10px;">
      <v-card-title>{{ message.sender }}</v-card-title>
      <v-card-subtitle>{{ new Date(Date.now()) }}</v-card-subtitle>
      <v-card-subtitle>{{ message.subtitle }}</v-card-subtitle>
      <v-card-text>{{ message.message }}</v-card-text>  
  	</v-card>

    </div>
</template>

<script>
export default {

  props: ['messages', 'isOnline'],

  data() {
    return {
      starting: null
    }
  },

  methods: {
    startBot() {

      this.messages.unshift({
        message: 'Запустить бота',
        sender: 'Оператор',
        subtitle: 'Команда'
      })

      this.$socket.emit('startBot')
      this.starting = true
    },
  }
}
</script>